* {
  padding: 0;
  box-sizing: border-box;
  font-weight: 600;
   margin: 0;
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
     'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
     sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
 }
 
 code {
   font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
     monospace;
     color: #555;
 }
 a{
   text-decoration: none;
   color: #555;
   transition: .2s ease;
 }

 .ul{
   list-style: none;
 
 }
 .App{
   flex: 1;
   max-width: 500px;
   width: 100%;
   min-height: 400px;
   box-shadow: 0 2px 6px #555;
   margin: 20px auto;
   padding: 50px;
 }

 h1{
   text-align: center;
   text-transform: uppercase;
   letter-spacing: 2px;
   font-weight: 900;
   color: #666;
 }
 form{
   height: 40px;
   width: 100%;
   margin: 20px 0;
   display: flex;
 }
 form input,
 form button{
   flex: 3;
   border: none;
   outline: none;
   border-bottom: 1px solid #555;
   margin: 0 5px;
 }
 form button{
   flex: 1;
   background: #555;
   color: white;
   letter-spacing: 2px;
   cursor: pointer;
   transition: all .8s ease;
 }
 form button:hover{
   background:#fff;
   box-shadow: 0 2px 6px #555;
   color: #555;
 }
 ul li{
   list-style: none;
   border: 1px solid #ccc;
   margin: 10px 0;
   padding: 10px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   text-transform: capitalize;
   box-shadow: 0 2px 6px #555;
 }

 ul li input,
 .row input{
   margin-right: 5px;
   transform: translateY(1px);
 }
 ul li button,
 .row button{
   border: none;
   outline: none;
   color: white;
   background: orange;
   padding: 5px 15px;
   cursor: pointer;
 }
 ul li button:disabled{
   opacity: 0.5;
   cursor: not-allowed;
 }
 .top_row ul{
   margin: 5px auto;
   display: flex;
   justify-content: center;
   align-self: center;
 }
 .top_row li{
   cursor: pointer;
   margin: 0 1px;
   border: none;
transition: all .5s ease;
box-shadow: none;
 }
 .top_row li:hover{
  background-color: #555;
 }
 .top_row li:hover a{
   color: #fff;
 }
.wrapp_for-top{
  display: flex;
  justify-content: space-between;
  align-self: center;
}
 .row{
   display: flex;
   justify-content: space-between;
   align-self: center;
   margin-top: 20px;
 }
 .row #delete{
   background: crimson;
 }
 .time{
   text-align: center;
 }
 .row button{
   margin: 0 5px;
 }
 .row-timer{
   display: flex;
   justify-content: space-around;
 }
 .app-timer{
  display: flex;
  flex-direction: column;
  justify-content: center;
 }
 label{
   cursor: pointer;
 }
.item_row{
  flex: 1;
  width: 100%;
   display: flex;
   justify-content: space-between;
   align-self: center;
 }

 @media (max-width: 500px){
  .top_row li{
    height: 7px;
    font-size: 7px;

       }
 }

 @media (max-width: 400px){
   .App{
     padding: 10px;
   }
   
 }


 